// app/page.tsx
import { createClient } from "@/lib/supabase/server"
import { Feed } from "@/components/feed/feed"
import { redirect } from "next/navigation"

export default async function RootPage() {
  const supabase = await createClient()

  const {
    data: { user },
  } = await supabase.auth.getUser()

  // Za neregistrovane korisnike, prika≈æite feed direktno
  if (!user) {
    return (
      <div className="min-h-screen">
        <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
          <div className="container flex h-14 items-center">
            <div className="mr-4 flex">
              <a className="mr-6 flex items-center space-x-2" href="/">
                <span className="font-bold">Iman App</span>
              </a>
            </div>
            <div className="flex flex-1 items-center justify-between space-x-2 md:justify-end">
              <a href="/login">
                <button className="inline-flex items-center justify-center rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-primary text-primary-foreground hover:bg-primary/90 h-9 px-4 py-2">
                  Login
                </button>
              </a>
            </div>
          </div>
        </header>
        
        <main className="container max-w-2xl mx-auto py-8">
          <div className="mb-8 text-center">
            <h1 className="text-3xl font-bold mb-2">Welcome to Iman App</h1>
            <p className="text-muted-foreground">
              Join our community to share and connect with others
            </p>
          </div>
          
          <Feed isAuthenticated={false} />
        </main>
      </div>
    )
  }

  // Ako je korisnik logovan, redirektuj na /home
  redirect("/home")
}